import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { Calendar } from "@happysanta/vk-app-ui";
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';

import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Header from '@vkontakte/vkui/dist/components/Header/Header';


const osName = platform();


const GroupInfo = ({id, go}) => (

	<Panel id={id}>
	<PanelHeader 
	left={<PanelHeaderButton onClick={go} data-to="home">
	{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
	</PanelHeaderButton>}
	>
	</PanelHeader>


	<Div align='center'>Когда день рождения у пользователя ?</Div>

	<Div align='center'>
	<Calendar onChange={onChange} />
	</Div>

	<Div align='center' className='happyDateDiv'></Div>
	<Div></Div>
	<Div></Div>


	</Panel>
	);

GroupInfo.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

const onChange = (e) => {
	let day = e._d.toString().substring(8, 10);
	let month = e._d.toString().substring(4, 7);
	let month_str;
	let month_date;

	switch (month) {
		case "Jan":
		month_str = "Января";
		month_date = 1;
		break;

		case "Feb":
		month_str = "Февраля";
		month_date = 2;
		break;

		case "Mar":
		month_str = "Марта";
		month_date = 3;
		break;

		case "Apr":
		month_str = "Апреля";
		month_date = 4;
		break;

		case "May":
		month_str = "Мая";
		month_date = 5;
		break;

		case "Jun":
		month_str = "Июня";
		month_date = 6;
		break;

		case "Jul":
		month_str = "Июоя";
		month_date = 7;
		break;

		case "Aug":
		month_str = "Августа";
		month_date = 8;
		break;

		case "Sep":
		month_str = "Сентября";
		month_date = 9;	
		break;

		case "Oct":
		month_str = "Октабря";
		month_date = 10;
		break;

		case "Nov":
		month_str = "Ноября";
		month_date = 11;
		break;

		case "Dec":
		month_str = "Декабря";
		month_date = 12;
		break;

		default:
		alert( "Нет таких значений" );
	}


	let str_res;
	str_res = e._d.toString().substring(0, 15);


	async function getGroupsUser() {
		const groupsUserConst = await bridge.send("VKWebAppCallAPIMethod", {
			"method": "users.search",
			"params": {
				group_id: window.obj_user_group_info.group_id,
				count: 1000,
				birth_day: day,
				birth_month: month_date,
				has_photo: 1,  
				fields:'photo_id,photo_50,photo_100,bdate',
				access_token: window.obj_user_group_info.user_auth_token,
				v: "5.103"
			}
		})

		console.log(groupsUserConst);
	};
	getGroupsUser();
	document.querySelector('.happyDateDiv').innerHTML = `<h3>${day} ${month_str}</h3>`;
};


export default GroupInfo;
